import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { Filter } from '../../interface-and-class/filter.interface';

const FILTERS: Filter[] = [];

@Component({
  selector: 'tmmed-filters',
  templateUrl: './filters.component.html',
  styleUrls: ['./filters.component.scss']
})
export class FiltersComponent implements OnInit {

    @Output() onChanged: EventEmitter<string[]> = new EventEmitter<string[]>();

    public filters = FILTERS;
    public showErrorEmpty = false;
    public showFilter = true;
    public filterToSend = [];

    public removeFilter(index: number) {
        for (const  value of this.filters) {
            if ( value.serialNumber > index) {
                value.serialNumber--;
            }
        }
        this.filters.splice(index, 1);
        this.filterToSend = [];
        for (let i = 0; i < this.filters.length; i++) {
            this.filterToSend[i] = this.filters[i].title;
        }
        this.onChanged.emit(this.filterToSend);
    }
    public addNewFilter(title: string) {
        if (title) {
            this.filters.push({ serialNumber: this.filters.length, title});
            this.filterToSend = [];
            for (let i = 0; i < this.filters.length; i++) {
                this.filterToSend[i] = this.filters[i].title;
            }
            this.showErrorEmpty = false;
            this.onChanged.emit(this.filterToSend);
        } else {
            this.showErrorEmpty = true;
        }
    }
    public createNewFilter(title: string) {
        if (title) {
            this.filters.push({ serialNumber: this.filters.length, title});
            this.filterToSend = [];
            for (let i = 0; i < this.filters.length; i++) {
                this.filterToSend[i] = this.filters[i].title;
            }
            this.showErrorEmpty = false;
            this.showFilter = true;
            this.onChanged.emit(this.filterToSend);
        } else {
            this.showErrorEmpty = true;
        }
    }
    constructor() {}

    ngOnInit() {
        this.filters = [];
        this.filterToSend = [];
    }

}
