import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { SettingsService } from 'src/app/service/settings.service';
import { Mkb10Service } from 'src/app/service/mkb10.service';

@Component({
  selector: 'tmmed-coverage-list-procedure',
  templateUrl: './coverage-list-procedure.component.html',
  styleUrls: ['./coverage-list-procedure.component.scss']
})
export class CoverageListProcedureComponent implements OnInit {

    @Input() service: any;
    @Input() coverageId: number;
    @Input() programId: number;
    @Output() onChanged = new EventEmitter<boolean>();

    public procedure: any = {
      id: null,
      procedureGroupId: null,
      monthLimit: {
        count: null,
        id: null,
        sum: null
      },
      onceFranchise: {
        id: null,
        maxSum: null,
        procent: null
      },
      onceLimit: {
        count: null,
        id: null,
        sum: null
      },
      programCoverId: null,
      yearFranchise: {
        id: null,
        maxSum: null,
        procent: null
      },
      yearLimit: {
        count: null,
        id: null,
        sum: null
      }
    };
    public procedureGroups: any[];
    public isReadonly = true;

    constructor(
      private _settingsservice: SettingsService,
      private _mkb10Service: Mkb10Service
    ) { }

    public deleteService() {
      this._settingsservice.deleteProcedureForCoverageById(this.coverageId, this.procedure.id).subscribe(
        data => {
          this.changeCoverage(data);
        },
        error => {
          console.log(error);
        }
      );
    }

    public changeService() {
        const arrayToSend = [];
        arrayToSend.push(this.procedure);
        this._settingsservice.postProcedureForCoverageById(arrayToSend, this.programId).subscribe(
            data => {
              this.changeCoverage(data);
              this.isReadonly = true;
            },
            error => {
                console.log(error);
            }
        );
    }

    public changeCoverage(data: any) {
      console.log(data);
      this.onChanged.emit(data);
    }

    ngOnInit() {
      this._mkb10Service.getListOfServiceGroup().subscribe(
        data => {
          this.procedureGroups = data.content;
        },
        error => {
          console.log(error);
        }
      );
      for (const field in this.service) {
        if (this.service.hasOwnProperty(field)) {
          this.procedure[field] = this.service[field];
        }
      }
    }

}
