import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';
import { DateToSendService } from '../../service/date-to-send.service';

@Component({
  selector: 'tmmed-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent implements OnInit {

    public productsList = [];
    public showButtonForNew = false;
    public amountOfProducts: number;
    public typeOfLength: string;
    public filtersToSend = [];
    public idOfDamage: any = this.router.url.substring(
        this.router.url.lastIndexOf('companies/') + 10,
        this.router.url.lastIndexOf('/products')
    );
    public nameOfCompany = '';
    updateList() {
        this._settingsService.getProductListByCompany(this.idOfDamage).subscribe(
            data => {
                this.productsList = data;
                this.amountOfProducts = data.length;
                this.typeOfLength = this._dateToSendService.checkAmountToShow(this.amountOfProducts);
                for (const product of this.productsList) {
                    product.selected = false;
                }
                if (this.productsList.length === 0) {
                    this.showButtonForNew = true;
                } else {
                    this.showButtonForNew = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }

    public setFilters(filters: any[]) {
        this.filtersToSend = filters;
        this.updateList();
    }

    public deleteCompany() {
        for (const product of this.productsList) {
            if (product.selected === true) {
                this._settingsService.deleteProductById(product.id).subscribe(
                    data => {
                        this.updateList();
                    },
                    error => {
                        console.log(error);
                    }
                );
            }
        }
    }
    public changeSelection() {
        for (const product of this.productsList) {
            product.selected = !product.selected;
        }
    }
    constructor(
        private _settingsService: SettingsService,
        private router: Router,
        private _dateToSendService: DateToSendService
    ) { }

    ngOnInit() {
        this.updateList();
        this._settingsService.getCompanyById(this.idOfDamage).subscribe(
            data => {
                this.nameOfCompany = data.companyName;
            },
            error => {
                console.log(error);
            }
        )
    }

}
