import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'tmmed-user-exception',
  templateUrl: './user-exception.component.html',
  styleUrls: ['./user-exception.component.scss']
})
export class UserExceptionComponent implements OnInit {

  @Input() exception: any;
  @Output() onSave = new EventEmitter<any>();

  public assuredExceptionItem: any = {}
  constructor() { }

  public console() {
    this.onSave.emit(this.assuredExceptionItem);
  }

  ngOnInit() {
    this.assuredExceptionItem = this.exception;
  }

}
