import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';
import { DateToSendService } from '../../service/date-to-send.service';
import { Program } from '../../interface-and-class/program.class'


@Component({
  selector: 'tmmed-program-id',
  templateUrl: './program-id.component.html',
  styleUrls: ['./program-id.component.scss']
})
export class ProgramIdComponent implements OnInit {

    public readonly = true;
    public program = new Program();

    public idOfProgram: any = this.router.url.substring(
        this.router.url.lastIndexOf('programs/') + 9
    );

    public changeProgram() {
        const programToSend: any = {
            code: this.program.code,
            dateEnd: this._dateToSend.makeDateToSend(this.program.dateEnd),
            companyProductId: this.program.companyProductId,
            dateStart: this._dateToSend.makeDateToSend(this.program.dateStart),
            name: this.program.name,
            type: 'Выплата',
            monthLimit: this.program.monthLimit,
            onceLimit: this.program.onceLimit,
            yearLimit: this.program.yearLimit,
            onceFranchise: this.program.onceFranchise,
            yearFranchise: this.program.yearFranchise
        }

        if (this.program !== undefined) {
            programToSend.id = this.program.id;
        }
        this._settingsservice.postProgramtById(programToSend).subscribe(
            data => {
                this.readonly = true;
                this.program.id = data;
                if (this.idOfProgram[0] === 'n') {
                    this.idOfProgram = data;
                    this.router.navigate([this.router.url.slice(0, -3), data], { replaceUrl: true });
                }
            },
            error => {
                console.log(error);
            }
        )
    }

    constructor(
        private _settingsservice: SettingsService,
        private router: Router,
        private _dateToSend: DateToSendService
    ) {}

    ngOnInit() {
        if (this.idOfProgram[0] !== 'n') {
            this._settingsservice.getProgramById(this.idOfProgram).subscribe(
                data => {
                    for (const i in data) { this.program[i] = data[i] }
                },
                error => {
                    console.log(error)
                }
            )
        } else {
            this.readonly = false;
            this.program.companyProductId = this.router.url.substring(
                this.router.url.lastIndexOf('products/') + 9,
                this.router.url.lastIndexOf('/programs')
            );
            this.program.type = 'Выплата';
        }
    }
}


