import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})

export class Mkb10Service {

  public treeOfMkb: any;
  public listOfMkb: any;
  public listOfMed: any;
  public listOfService: any;
  public listOfMedGroup: any;
  public listOfServiceGroup: any;
  public listOfClinic: any;

  serverUrl = environment.serverUrl;

 constructor(private httpClient: HttpClient) {
    this.httpClient.get(this.serverUrl + '/classifiers/icd10').subscribe(
        data => {
            this.treeOfMkb = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/icd10?type=list').subscribe(
        data => {
            this.listOfMkb = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/medicaments?paged=false').subscribe(
        data => {
            this.listOfMed = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/procedures?paged=false').subscribe(
        data => {
            this.listOfService = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/medicaments/groups?paged=false').subscribe(
        data => {
            this.listOfMedGroup = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/procedures/groups?paged=false').subscribe(
        data => {
            this.listOfServiceGroup = data;
        },
        error => {
            console.log(error);
        }
    );
    this.httpClient.get(this.serverUrl + '/classifiers/clinics').subscribe(
        data => {
            this.listOfClinic = data;
        },
        error => {
            console.log(error);
        }
    );
 }

    public getMkb10() {
        return this.getList(this.treeOfMkb, '/classifiers/icd10');
    }

    public getMkbList() {
        return this.getList(this.listOfMkb, '/classifiers/icd10?type=list');
    }

    public getListOfMeds() {
        return this.getList(this.listOfMed, '/classifiers/medicaments?paged=false');
    }

    public getListOfService() {
        return this.getList(this.listOfService, '/classifiers/procedures?paged=false');
    }

    public getListOfMedsGroup() {
        return this.getList(this.listOfMedGroup, '/classifiers/medicaments/groups?paged=false');
    }

    public getListOfServiceGroup() {
        return this.getList(this.listOfServiceGroup, '/classifiers/procedures/groups?paged=false');
    }

    public getListOfClinic() {
        return this.getList(this.listOfClinic, '/classifiers/clinics');
    }

    public justReturn(list: any): any {
        const listForReturning = new Observable(observer => {
                setTimeout(() => {
                    observer.next(list);
                }, 100);
        });
        return listForReturning;
    }

    public getList(list: any, url: string) {
        if (list === undefined) {
            return this.httpClient.get(this.serverUrl + url);
        } else {
            return this.justReturn(list);
        }
    }
}
