import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { DateToSendService } from 'src/app/service/date-to-send.service';
import { Mkb10Service } from 'src/app/service/mkb10.service';

@Component({
  selector: 'tmmed-user-exception-procedure-groups',
  templateUrl: './user-exception-procedure-groups.component.html',
  styleUrls: ['./user-exception-procedure-groups.component.scss']
})
export class UserExceptionProcedureGroupsComponent implements OnInit {

  @Input() exception: any;
  @Output() onUpdate = new EventEmitter<any>();

  public procedureGroups: any[] = [];
  public isAdding = false;
  public showSpinerProcedureGroup = true;

  public controlForProcedureGroup = new FormControl();
  public optionsForProcedureGroup: any[] = [];
  public filteredOptionsForProcedureGroup: Observable<any[]>;
  public tempOption = {
    id: null,
    procedureGroupItem: {
      code: null,
      description: null,
      id: null,
      name: null
    }
  }
  constructor(
    public _dateToSend: DateToSendService,
    private _mkb10Service: Mkb10Service
  ) { }

  public clearInput() {
    this.controlForProcedureGroup.setValue({ name: '' });
    this.tempOption = {
      id: null,
      procedureGroupItem: {
        code: null,
        description: null,
        id: null,
        name: null
      }
    }
  }
  public addGroup() {
    if (this.tempOption.procedureGroupItem.id != null) {
      this.procedureGroups.unshift(this.tempOption);
      this.clearInput();
      this.isAdding = false;
      this.onUpdate.emit(this.procedureGroups);
    }
  }
  public deleteGroup(group: any) {
    this.procedureGroups.splice(this.procedureGroups.indexOf(group), 1);
      this.onUpdate.emit(this.procedureGroups);
  }
  public chooseOption(option: any) {
    this.tempOption.procedureGroupItem = option;
  }
  ngOnInit() {
    this.procedureGroups = this.exception;
    this._mkb10Service.getListOfServiceGroup().subscribe(
      data => {
        for (const value of data.content) {
          this.optionsForProcedureGroup.push(value);
        }
        this.filteredOptionsForProcedureGroup = this._dateToSend.makeListForFilters(
          this.controlForProcedureGroup, 'name', this.optionsForProcedureGroup, 0
        );
        this.showSpinerProcedureGroup = false;
      },
      error => { console.log(error); }
    );
  }

}
