import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Mkb10Service } from '../../service/mkb10.service'
import { SettingsService } from 'src/app/service/settings.service';

@Component({
  selector: 'tmmed-coverage-list-med-add',
  templateUrl: './coverage-list-med-add.component.html',
  styleUrls: ['./coverage-list-med-add.component.scss']
})
export class CoverageListMedAddComponent implements OnInit {

    @Input() coverageId: number;
    @Input() programId: number;
    @Output() onCancel = new EventEmitter<boolean>();
    @Output() onUpdate = new EventEmitter<any>();

    public medicamentGroups: any[];
    public id: number;
    public med = {
        programCoverId: this.coverageId,
        medicamentGroupId: '',
        onceFranchise: {
            maxSum: null,
            procent: null
        },
        onceLimit: {
            sum: null
        },
        monthLimit: {
            sum: null
        },
        yearLimit: {
            sum: null
        }
    }
    constructor(
        private _mkb10Service: Mkb10Service,
        private _settingsservice: SettingsService
    ) { }

    public addNewMed() {
        const arrayToSend = [];
        arrayToSend.push(this.med);
        this._settingsservice.postMedicamentForCoverageById(arrayToSend, this.programId).subscribe(
            data => {
                this.med = {
                    programCoverId: this.coverageId,
                    medicamentGroupId: '',
                    onceFranchise: {
                        maxSum: null,
                        procent: null
                    },
                    onceLimit: {
                        sum: null
                    },
                    monthLimit: {
                        sum: null
                    },
                    yearLimit: {
                        sum: null
                    }
                }
                this.deleteNewMed();
                this.onUpdate.emit(data);
            },
            error => {
                console.log(error);
            }
        );
    }
    public deleteNewMed() {
        this.onCancel.emit();
    }

    ngOnInit() {
        this.med.programCoverId = this.coverageId;
        this._mkb10Service.getListOfMedsGroup().subscribe(
            data => {
                this.medicamentGroups = data.content;
            },
            error => {
                console.log(error);
            }
        );
    }

}
