import { Component } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { LosesService } from '../../service/loses.service';
import { Mkb10Service } from '../../service/mkb10.service';
import { UsersService } from '../../service/users.service';
import { DateToSendService } from '../../service/date-to-send.service';
import { Loss } from '../../interface-and-class/forloss.class';

@Component({
    selector: 'tmmed-loss-id',
    templateUrl: './loss-id.component.html',
    styleUrls: ['./loss-id.component.scss']
})

export class LossIdComponent {
    public loss = new Loss();
    public lossListForUser: Array<any>;
    public sumOfService = 0;
    public sumOfMed = 0;
    public idOfLoss: string = this._router.url.slice(8);

    public controlForDiagnosisInModal = new FormControl();
    public optionsForDiagnosisModal: any[] = [];
    public filteredOptionsForDiagnosisModal: Observable<any[]>;

    public controlForDiagnosisCodeInModal = new FormControl();
    public optionsForDiagnosisCodeInModal: any[] = [];
    public filteredOptionsForDiagnosisCodeInModal: Observable<any[]>;

    public controlForDiagnosis = new FormControl();
    public optionsForDiagnosis: any[] = [];
    public filteredOptionsForDiagnosis: Observable<any[]>;

    public controlForDiagnosisCode = new FormControl();
    public optionsForDiagnosisCode: any[] = [];
    public filteredOptionsForDiagnosisCode: Observable<any[]>;

    public controlForClinic = new FormControl();
    public optionsForClinic: any[] = [];
    public filteredOptionsForClinic: Observable<any[]>;

    public controlForInn = new FormControl();
    public optionsForInn: any[] = [];
    public filteredOptionsForInn: Observable<any[]>;

    public showSpinerDiagnosis = true;
    public showSpinerCode = true;
    public showSpinerClinic = true;
    public showSpinerInn = true;

    constructor(
        private _losesservice: LosesService,
        private _mkb10Service: Mkb10Service,
        private _usersService: UsersService,
        private _router: Router,
        public _dateToSend: DateToSendService
    ) {
        if (this.idOfLoss[0] === 'n') {
            this.loss = this._losesservice.getNew();
            this._usersService.getUserById(this.idOfLoss.slice(4)).subscribe(
                data => {
                    const today = new Date();
                    this.loss.date = this._dateToSend.makeDateToSend(today);
                    this.loss.id = data.id;
                    this.loss.assured = data;
                    this.loss.stateId = 1;
                },
                error => { console.log(error); }
            );
            this._losesservice.getAllLossesForUser(this.idOfLoss.slice(4)).subscribe(
                data => {
                    this.idOfLoss = null;
                    this.lossListForUser = data;
                },
                error => { console.log(error); }
            );
        } else {
            this._losesservice.getLossesById(this.idOfLoss).subscribe(
                data => {
                    this.loss = data;
                    this.loss.diagnosisId = this.loss.diagnosis.id;
                    this.controlForDiagnosis.setValue({ name: this.loss.diagnosis.name });
                    this.controlForDiagnosisCode.setValue({ code: this.loss.diagnosis.code });
                    this.controlForClinic.setValue({ clinicName: this.loss.clinic.clinicName });
                    this.controlForInn.setValue({ inn: this.loss.clinic.inn });
                    for (const value of this.loss.medicaments) {
                        value.readonly = true;
                    }
                    for (const value of this.loss.procedures) {
                        value.readonly = true;
                    }
                    this._losesservice.getAllLossesForUser(data.assured.id).subscribe(
                        data => {
                            this.lossListForUser = data;
                        },
                        error => { console.log(error); }
                    );
                },
                error => { console.log(error); }
            );
        }
        this._mkb10Service.getMkbList().subscribe(
            data => {
                for (const value of data) {
                    if (value.nodeCount === 0) {
                        this.optionsForDiagnosis.push(value);
                        this.optionsForDiagnosisModal.push(value);
                        this.optionsForDiagnosisCode.push(value);
                        this.optionsForDiagnosisCodeInModal.push(value);
                    }
                }
                this.filteredOptionsForDiagnosis = this._dateToSend.makeListForFilters(
                    this.controlForDiagnosis, 'name', this.optionsForDiagnosis, 3
                );
                this.filteredOptionsForDiagnosisModal = this._dateToSend.makeListForFilters(
                    this.controlForDiagnosisInModal, 'name', this.optionsForDiagnosisModal, 3
                );
                this.filteredOptionsForDiagnosisCode = this._dateToSend.makeListForFilters(
                    this.controlForDiagnosisCode, 'code', this.optionsForDiagnosisCode, 2
                );
                this.filteredOptionsForDiagnosisCodeInModal = this._dateToSend.makeListForFilters(
                    this.controlForDiagnosisCodeInModal, 'code', this.optionsForDiagnosisCodeInModal, 2
                );
                this.showSpinerDiagnosis = false;
                this.showSpinerCode = false;
            },
            error => { console.log(error); }
        );
        this._mkb10Service.getListOfClinic().subscribe(
            data => {
                for (const value of data) {
                    this.optionsForClinic.push(value);
                    this.optionsForInn.push(value);
                }
                this.filteredOptionsForClinic = this._dateToSend.makeListForFilters(
                    this.controlForClinic, 'clinicName', this.optionsForClinic, 0
                );
                this.filteredOptionsForInn = this._dateToSend.makeListForFilters(
                    this.controlForInn, 'inn', this.optionsForInn, 0
                );
                this.showSpinerClinic = false;
                this.showSpinerInn = false;
            },
            error => { console.log(error); }
        );

    }

    public chooseDiagnosis(option: any) {
        this.loss.diagnosis.name = option.name;
        this.loss.diagnosis.code = option.code;
        this.loss.diagnosis.description = option.description;
        this.loss.diagnosisId = option.id;
    }
    public chooseClinic(option: any) {
        this.loss.clinic.clinicName = option.clinicName;
        this.loss.clinic.clinicCode = option.clinicCode;
        this.loss.clinic.bik = option.bik;
        this.loss.clinic.inn = option.inn;
        this.loss.clinic.id = option.id;
        this.controlForInn.setValue({ inn: option.inn });
        this.controlForClinic.setValue({ clinicName: option.clinicName });
    }
    public updateLoss(data: any) {
        this.loss.amount = data.amount;
        this.loss.insurance = data.insurance;
        this.loss.procedures = data.procedures;
        this.loss.medicaments = data.medicaments;
        this.loss.amountProcedures = data.amountProcedures;
        this.loss.amountMedicaments = data.amountMedicaments;
        this.loss.insuranceProcedures = data.insuranceProcedures;
        this.loss.insuranceMedicaments = data.insuranceMedicaments;
        for (const value of this.loss.procedures) {
            value.readonly = true;
        }
        for (const value of this.loss.medicaments) {
            value.readonly = true;
        }
    }

    public changeStatus(status: number) {
        const statusToSend = {
            stateId: status
        };
        this._losesservice.changeStatus(this.idOfLoss, statusToSend).subscribe(
            error => { console.log(error); }
        );
    }
    public sendThisLoss() {
        const lossToSend = {
            assuredId: this.loss.assured.id,
            date: this._dateToSend.makeDateToSend(this.loss.date),
            diagnosisId: this.loss.diagnosisId,
            id: this.idOfLoss,
            medicaments: this.loss.medicaments,
            procedures: this.loss.procedures,
            stateId: this.loss.stateId,
            note: this.loss.note
        };
        this._losesservice.postDamage(lossToSend).subscribe(
            data => {},
            error => { console.log(error); }
        );
    }
    public sendThisNewLoss() {
        const lossToSend = {
            assuredId: this.loss.assured.id,
            date: this._dateToSend.makeDateToSend(this.loss.date),
            diagnosisId: this.loss.diagnosisId,
            id: this.idOfLoss,
            medicaments: this.loss.medicaments,
            procedures: this.loss.procedures,
            remainingLimit: this.loss.remainingLimit,
            diagnosisLimit: this.loss.diagnosisLimit,
            clinicId: this.loss.clinic.id,
            note: this.loss.note,
            stateId: this.loss.stateId
        };
        this._losesservice.postDamage(lossToSend).subscribe(
            data => {
                this._router.navigate(['../../losses', data.id], { replaceUrl: true });
            },
            error => { console.log(error); }
        );
    }

    public goToLoss(damage: any) {
        this._router.navigate(['/losses', damage.id]);
    }

    public console(x) {
        console.log(x);
    }
}
