import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { Mkb10Service } from 'src/app/service/mkb10.service';
import { DateToSendService } from 'src/app/service/date-to-send.service';

@Component({
  selector: 'tmmed-user-exception-procedure',
  templateUrl: './user-exception-procedure.component.html',
  styleUrls: ['./user-exception-procedure.component.scss']
})
export class UserExceptionProcedureComponent implements OnInit {

  @Input() exception: any;
  @Output() onUpdate = new EventEmitter<any>();

  public procedures: any[] = [];
  public tempProcedure = {
    id: null,
    procedureItem: {
      code: null,
      groupId: null,
      groupName: null,
      id: null,
      name: null
    }
  };
  public isAdding = false;

  public controlForService = new FormControl();
  public optionsForService: any[] = [];
  public filteredOptionsForService: Observable<any[]>;

  public controlForServiceCode = new FormControl();
  public optionsForServiceCode: any[] = [];
  public filteredOptionsForServiceCode: Observable<any[]>;
  public showSpinerService = true;

  constructor(
    private _mkb10Service: Mkb10Service,
    public _dateToSend: DateToSendService
  ) {
    this._mkb10Service.getListOfService().subscribe(
        data => {
            for (const value of data.content) {
                this.optionsForService.push(value);
                this.optionsForServiceCode.push(value);
            }
            this.filteredOptionsForService = this._dateToSend.makeListForFilters(
                this.controlForService, 'name', this.optionsForService, 3
            );
            this.filteredOptionsForServiceCode = this._dateToSend.makeListForFilters(
                this.controlForServiceCode, 'code', this.optionsForServiceCode, 3
            );
            this.showSpinerService = false;
        },
        error => { console.log(error); }
    );
  }
  public clearInput() {
    this.controlForService.setValue({ name: '' });
    this.controlForServiceCode.setValue({ code: '' });
    this.tempProcedure = {
      id: null,
      procedureItem: {
        code: null,
        groupId: null,
        groupName: null,
        id: null,
        name: null
      }
    };
  }
  public addProcedure() {
    if (this.tempProcedure != null) {
      this.procedures.unshift(this.tempProcedure);
      this.clearInput();
      this.isAdding = false;
      this.onUpdate.emit(this.procedures);
    }
  }
  public deleteProcedure(procedure: any) {
    this.procedures.splice(this.procedures.indexOf(procedure), 1);
    this.onUpdate.emit(this.procedures);
  }
  public chooseOption(option: any) {
    this.tempProcedure.procedureItem = option;
    this.controlForServiceCode.setValue({ code: option.code });
    this.controlForService.setValue({ name: option.name });
  }
  ngOnInit() {
    this.procedures = this.exception;
  }

}
