import { Injectable } from '@angular/core';
import { FormControl } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DateToSendService {

    constructor() { }

    public makeDateToSend(date: any) {
        if (typeof date === 'string') {
            return date;
        } else {
            let month: any = date.getMonth();
            let day: any = date.getDate();
            month++;
            if (month < 10) {
            month = '0' + month;
            }
            if ( day < 10) {
            day = '0' + day;
            }
            return date.getFullYear().toString() + '-' + month + '-' + day;
        }
    }
    checkAmountToShow(amount: any) {
        if (amount % 100 === 11 ||
            amount % 100 === 12 ||
            amount % 100 === 13 ) {
            return 'записей';
        } else if (amount % 10 === 1) {
            return 'запись';
        } else if (amount % 10 === 2 ||
                   amount % 10 === 3 ||
                   amount % 10 === 4) {
            return 'записи';
        } else {
            return 'записей';
        }
    }
    public _filter(name: string, options: any[]): any[] {
        const filterValue = name.toLowerCase();
        return options.filter(option => option.name.toLowerCase().indexOf(filterValue) === 0);
    }

    public _filterCode(name: string, options: any[]): any[] {
        const filterValue = name.toLowerCase();
        return options.filter(option => option.code.toLowerCase().indexOf(filterValue) === 0);
    }

    public _filterClinic(name: string, options: any[]): any[] {
        const filterValue = name.toLowerCase();
        return options.filter(option => option.clinicName.toLowerCase().indexOf(filterValue) === 0);
    }

    public _filterInn(name: string, options: any[]): any[] {
        const filterValue = name.toLowerCase();
        return options.filter(option => option.inn.toString().toLowerCase().indexOf(filterValue) === 0);
    }

    public _filterCompanyName(name: string, options: any[]): any[] {
        const filterValue = name.toLowerCase();
        return options.filter(option => option.companyName.toLowerCase().indexOf(filterValue) === 0);
    }

    public makeListForFilters(control: FormControl, type: any, options: any, amount: number) {
        if (type === 'name') {
            return control.valueChanges.pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.name),
                map(name => name.length >= amount ? this._filter(name, options) : [])
            );
        } else if (type === 'code') {
            return control.valueChanges.pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.code),
                map(code => code.length >= amount ? this._filterCode(code, options) : [])
            );
        } else if (type === 'clinicName') {
            return control.valueChanges.pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.clinicName),
                map(clinicName => clinicName.length >= amount ? this._filterClinic(clinicName, options) : [])
            );
        } else if (type === 'inn') {
            return control.valueChanges.pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.inn),
                map(inn => inn.length >= amount ? this._filterInn(inn, options) : [])
            );
        } else if (type === 'companyName') {
            return control.valueChanges.pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value.companyName),
                map(companyName => companyName.length >= amount ? this._filterCompanyName(companyName, options) : [])
            );
        }
    }

    public displayFn(user?: any): string | undefined {
        return user ? user.name : undefined;
    }

    public displayFnCode(user?: any): string | undefined {
        return user ? user.code : undefined;
    }

    public displayFnClinic(user?: any): string | undefined {
        return user ? user.clinicName : undefined;
    }

    public displayFnInn(user?: any): string | undefined {
        return user ? user.inn : undefined;
    }

    public displayFnCompanyName(user?: any): string | undefined {
        return user ? user.companyName : undefined;
    }
}
