import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { ModalMed } from '../../interface-and-class/forloss.class';
import { Mkb10Service } from 'src/app/service/mkb10.service';
import { DateToSendService } from 'src/app/service/date-to-send.service';
import { LosesService } from 'src/app/service/loses.service';
import { Router } from '@angular/router';

@Component({
  selector: 'tmmed-loss-id-med-add',
  templateUrl: './loss-id-med-add.component.html',
  styleUrls: ['./loss-id-med-add.component.scss']
})
export class LossIdMedAddComponent implements OnInit {

    @Output() onChanged = new EventEmitter<boolean>();

    public modalMed = new ModalMed();
    public idOfLoss: string = this.router.url.slice(8);

    public showSpinerMed = true;
    public showSpinerMedGroup = true;
    public showModalSaveMed = false;
    public isDisabledMedicamentInfo = true;

    public controlForMeds = new FormControl();
    public optionsForMeds: any[] = [];
    public filteredOptionsForMeds: Observable<any[]>;

    public controlForMedsCode = new FormControl();
    public optionsForMedsCode: any[] = [];
    public filteredOptionsForMedsCode: Observable<any[]>;

    public controlForMedGroup = new FormControl();
    public optionsForMedGroup: any[] = [];
    public filteredOptionsForMedGroup: Observable<any[]>;

    constructor(
        private _mkb10Service: Mkb10Service,
        public _dateToSend: DateToSendService,
        private _losesservice: LosesService,
        private router: Router
    ) {
        this._mkb10Service.getListOfMeds().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForMeds.push(value);
                    this.optionsForMedsCode.push(value);
                }
                this.filteredOptionsForMeds = this._dateToSend.makeListForFilters(this.controlForMeds, 'name', this.optionsForMeds, 3);
                this.filteredOptionsForMedsCode = this._dateToSend.makeListForFilters(
                    this.controlForMedsCode, 'code', this.optionsForMedsCode, 3
                );
                this.showSpinerMed = false;
            },
            error => { console.log(error); }
        );

        this._mkb10Service.getListOfMedsGroup().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForMedGroup.push(value);
                }
                this.filteredOptionsForMedGroup = this._dateToSend.makeListForFilters(
                    this.controlForMedGroup, 'name', this.optionsForMedGroup, 0
                );
                this.showSpinerMedGroup = false;
            },
            error => { console.log(error); }
        );
    }

    public checkModalMedSave() {
        if (this.modalMed.medicamentSaveItem.name === '' ||
            this.modalMed.stateId === 0 ||
            this.modalMed.medicamentSaveItem.groupName === '' ||
            this.modalMed.date === '' ||
            this.modalMed.medicamentSaveItem.name === null ||
            this.modalMed.medicamentSaveItem.groupName === null) {
            this.showModalSaveMed = false;
        } else {
            this.showModalSaveMed = true;
        }
    }

    public chooseMedFromList(option: any) {
        this.controlForMedsCode.setValue({ code: option.code });
        this.controlForMedGroup.setValue({ name: option.groupName });
        this.modalMed.medicamentSaveItem.name = option.name;
        this.modalMed.medicamentSaveItem.code = option.code;
        this.modalMed.medicamentSaveItem.groupName = option.groupName;
        this.modalMed.medicamentSaveItem.groupId = option.groupId;
        this.modalMed.medicamentSaveItem.medicamentForm = option.medicamentForm;
        this.modalMed.medicamentSaveItem.id = option.id;
    }

    public chooseMedFromListCode(option: any) {
        this.controlForMeds.setValue({ name: option.name });
        this.controlForMedGroup.setValue({ name: option.groupName });
        this.modalMed.medicamentSaveItem.name = option.name;
        this.modalMed.medicamentSaveItem.code = option.code;
        this.modalMed.medicamentSaveItem.groupName = option.groupName;
        this.modalMed.medicamentSaveItem.groupId = option.groupId;
        this.modalMed.medicamentSaveItem.medicamentForm = option.medicamentForm;
        this.modalMed.medicamentSaveItem.id = option.id;
    }

    public addNewMed() {
        this.modalMed.amount = this.modalMed.costPerUnit * this.modalMed.quantityAmount;
        this.modalMed.date = this._dateToSend.makeDateToSend(this.modalMed.date);
        const arrayToSend = [];
        arrayToSend.push(this.modalMed);
        this._losesservice.addNewMed(this.idOfLoss, arrayToSend).subscribe(
            data => {
                this.onChanged.emit(data);
                this.modalMed = new ModalMed();
                this.clearModal();
            },
            error => { console.log(error); }
        );
    }

    public clearModal() {
        this.controlForMeds.setValue({ name: '' });
        this.controlForMedsCode.setValue({ code: '' });
        this.controlForMedGroup.setValue({ name: '' });
    }

    ngOnInit() {
    }

}
