 import { Component, OnInit } from '@angular/core';
 import { LosesService } from '../../service/loses.service';
 import { DateToSendService } from '../../service/date-to-send.service';

 @Component({
    selector: 'tmmed-loss-list',
    templateUrl: './loss-list.component.html',
    styleUrls: ['./loss-list.component.scss']
})

export class LossListComponent implements OnInit {

    public damages: Array<any>;
    public idToDelete: any;
    public amountOfDamages = 0;
    public typeOfAmountToShow = ''
    public filtersToSend = [];

    constructor(
        private _losesservice: LosesService,
        private _dateToSend: DateToSendService
    ) {}

    public updateList() {
        this._losesservice.getLoses(this.filtersToSend).subscribe(
            data => {
                this.damages = data.content;
                this.amountOfDamages = this.damages.length;
                this.typeOfAmountToShow = this._dateToSend.checkAmountToShow(this.amountOfDamages);
                for (const value of this.damages) {
                    value.selected = false;
                }
            },
            error => { console.log(error); }
        );
    }
    public setFilters(filters: any[]) {
        this.filtersToSend = filters;
        this.updateList();
    }

    public changeStatus( id: string, stateId: number) {
        const statusToSend = {
            stateId: stateId
        };
        this._losesservice.changeStatus(id, statusToSend).subscribe(
            resp => {
                if (resp.status === 200) {
                    for (const value of this.damages) {
                        if (value.id === id) {
                            value.stateId = statusToSend.stateId;
                        }
                    }
                }
            },
            error => { console.log(error); }
        );
    }

    public deleteLoss() {
        for (const value of this.damages) {
            if (value.selected) {
                this._losesservice.deleteLoss(value.id).subscribe(
                    data => {
                        this.updateList();
                    },
                    error => { console.log(error); }
                );
            }
        }
    }

    public checkAll() {
        for (const value of this.damages) {
            value.selected = !value.selected;
        }
    }

    ngOnInit() {
        this.updateList();
    }

}
