import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Mkb10Service } from '../../service/mkb10.service'
import { SettingsService } from 'src/app/service/settings.service';

@Component({
  selector: 'tmmed-coverage-list-procedure-add',
  templateUrl: './coverage-list-procedure-add.component.html',
  styleUrls: ['./coverage-list-procedure-add.component.scss']
})

export class CoverageListProcedureAddComponent implements OnInit {

    @Input() coverageId: number;
    @Input() programId: number;
    @Output() onCancel = new EventEmitter<boolean>();

    public procedureGroups: any[];
    public service = {
        programCoverId: this.coverageId,
        procedureGroupId: '',
        onceFranchise: {
            maxSum: null,
            procent: null
        },
        onceLimit: {
            sum: null
        },
        monthLimit: {
            sum: null
        },
        yearLimit: {
            sum: null
        }
    }
    constructor(
        private _mkb10Service: Mkb10Service,
        private _settingsservice: SettingsService
    ) { }

    public addNewService() {
        const arrayToSend = [];
        arrayToSend.push(this.service);
        this._settingsservice.postProcedureForCoverageById(arrayToSend, this.programId).subscribe(
            data => {
                this.service = {
                    programCoverId: this.coverageId,
                    procedureGroupId: '',
                    onceFranchise: {
                        maxSum: null,
                        procent: null
                    },
                    onceLimit: {
                        sum: null
                    },
                    monthLimit: {
                        sum: null
                    },
                    yearLimit: {
                        sum: null
                    }
                }
                this.deleteNewService();
            },
            error => {
                console.log(error);
            }
        );
    }
    public deleteNewService() {
        this.onCancel.emit();
    }
    ngOnInit() {
        this.service.programCoverId = this.coverageId;
        this._mkb10Service.getListOfServiceGroup().subscribe(
            data => {
                this.procedureGroups = data.content;
            },
            error => {
                console.log(error);
            }
        );
    }

}
