import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'tmmed-setting-tree-procedure',
  templateUrl: './setting-tree-procedure.component.html',
  styleUrls: ['./setting-tree-procedure.component.scss']
})
export class SettingTreeProcedureComponent implements OnInit {

    @Input() procedure: any;
    @Output() onChanged = new EventEmitter<boolean>();

    public service: any;

    public makeDefault() {
        this.service.readonly = true;
        this.service.makeToDelete = false;
    }

    public makeToChange() {
        this.service.readonly = false;
        this.service.makeToDelete = false;
    }

    public makeToAskToDelete() {
        this.service.readonly = true;
        this.service.makeToDelete = true;
    }

    constructor() { }

    ngOnInit() {
        this.service = this.procedure;
        this.service.readonly = true;
        this.service.makeToDelete = false;
    }

}




