import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';
import { DateToSendService } from '../../service/date-to-send.service';

@Component({
  selector: 'tmmed-product-id',
  templateUrl: './product-id.component.html',
  styleUrls: ['./product-id.component.scss']
})

export class ProductIdComponent implements OnInit {


    public readonly = true;
    public idOfProdact: any = this.router.url.substring(
        this.router.url.lastIndexOf('products/') + 9
    );
    public product = {
        name: '',
        id: null,
        insuredCompanyId: null,
        dateEnd: '',
        dateStart: '',
        version: null,
        document: ''
    };

    public changeProduct() {
        this.product.dateEnd = this._dateToSend.makeDateToSend(this.product.dateEnd);
        this.product.dateStart = this._dateToSend.makeDateToSend(this.product.dateStart);
        this._settingsService.postProductById(this.product).subscribe(
            data => {
                if (this.idOfProdact[0] === 'n') {
                    this.readonly = true;
                    this.router.navigate(['../companies/' + this.product.insuredCompanyId + '/products/' + data], { replaceUrl: true });
                } else {
                    this.readonly = true;
                }
            },
            error => { console.log(error); }
        )
    }
    constructor(
        private router: Router,
        private _settingsService: SettingsService,
        private _dateToSend: DateToSendService
    ) {}

    ngOnInit() {
        if (this.idOfProdact[0] === 'n') {
            this.readonly = false;
            this.product.insuredCompanyId = this.router.url.substring(
                this.router.url.lastIndexOf('companies/') + 10,
                this.router.url.lastIndexOf('/products')
            );
        } else {
            this._settingsService.getProductById(this.idOfProdact).subscribe(
                data => {
                    this.product.name = data.name;
                    this.product.id = data.id;
                    this.product.insuredCompanyId = data.insuredCompanyId;
                    this.product.dateEnd = data.dateEnd;
                    this.product.dateStart = data.dateStart;
                    this.product.version = data.version;
                    this.product.document = data.document;
                },
                error => { console.log(error); }
            )
        }
    }
}

