import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UsersService } from '../../service/users.service';
import { LosesService } from '../../service/loses.service';
import { DateToSendService } from '../../service/date-to-send.service';
import { User } from '../../interface-and-class/user.class'
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { Mkb10Service } from '../../service/mkb10.service';
import { FlatNode, Node } from '../../interface-and-class/tree.interface';
import { SettingsService } from 'src/app/service/settings.service';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';

@Component({
  selector: 'tmmed-user-id',
  templateUrl: './user-id.component.html',
  styleUrls: ['./user-id.component.scss']
})
export class UserIdComponent implements OnInit {

    public modalUser = new User();
    public newUser = true;
    public readonly = true;
    public idOfUser: string = this.router.url.slice(7);

    public controlForCompany = new FormControl();
    public optionsForCompany: any[] = [];
    public filteredOptionsForCompany: Observable<any[]>;

    public controlForProduct = new FormControl();
    public optionsForProduct: any[] = [];
    public filteredOptionsForProduct: Observable<any[]>;

    public controlForProgram = new FormControl();
    public optionsForProgram: any[] = [];
    public filteredOptionsForProgram: Observable<any[]>;

    public showSpinerCompany = true;
    public showSpinerProduct = true;
    public showSpinerProgram = true;

    public assuredExceptionItem: any = null;

    constructor(
        private _usersservice: UsersService,
        private router: Router,
        private _snackBar: MatSnackBar,
        private _losesService: LosesService,
        public _dateToSend: DateToSendService,
        private _mkb10Service: Mkb10Service,
        private _settingsService: SettingsService
    ) {
        this._mkb10Service.getMkb10().subscribe(
            data => {
                this.dataSource.data = data;
            },
            error => { console.log(error); }
        );
    }

    hasChild = (_: number, node: FlatNode) => node.expandable;
    public transformer = (node: Node, level: number) => {
        return {
            expandable: !!node.nodes && node.nodes.length > 1,
            name: node.name,
            level: level,
            description: node.description,
            code: node.code,
            id: node.id
        };
    }

    public treeControl = new FlatTreeControl<FlatNode>(node => node.level, node => node.expandable);
    public treeFlattener = new MatTreeFlattener(this.transformer, node => node.level, node => node.expandable, node => node.nodes);
    public dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    public sendUser() {
        this.modalUser.birthDate = this._dateToSend.makeDateToSend(this.modalUser.birthDate);
        this.modalUser.programStartDate = this._dateToSend.makeDateToSend(this.modalUser.programStartDate);
        this.modalUser.programEndDate = this._dateToSend.makeDateToSend(this.modalUser.programEndDate);
        if ( this.modalUser.firstName === '' ||
             this.modalUser.lastName === '' ||
             this.modalUser.phone === '' ||
             this.modalUser.snils === '' ||
             this.modalUser.isMan === null ||
             this.modalUser.insuredCompanyProductProgramId === null ||
             this.modalUser.programNumber === '' ||
             this.modalUser.programStartDate === '' ||
             this.modalUser.programEndDate === '') {
                this._snackBar.open('Необходимо заполнить все обязательные поля помеченные *', '', {
                    duration: 2000,
                });
        } else {
            const userToSend: any = {
                firstName: this.modalUser.firstName,
                lastName: this.modalUser.lastName,
                middleName: this.modalUser.middleName,
                phone: this.modalUser.phone,
                birthDate: this.modalUser.birthDate,
                snils: this.modalUser.snils,
                isMan: this.modalUser.isMan,
                insuredCompanyProductProgramId: this.modalUser.insuredCompanyProductProgramId,
                programNumber: this.modalUser.programNumber,
                programStartDate: this.modalUser.programStartDate,
                programEndDate: this.modalUser.programEndDate,
                cardNumber: this.modalUser.cardNumber
            }
            if ( this.newUser === false ) {
                userToSend.id = this.modalUser.id;
            }
            this._usersservice.sendUser(userToSend).subscribe(
                data => {
                    this.readonly = true;
                    if (this.newUser === true) {
                        this.router.navigate(['../users', data], { replaceUrl: true });
                    }
                },
                error => {
                    console.log(error);
                }
            );

        }
    }

    public console(data: any) {
        this.assuredExceptionItem = data;
    }

    public saveExceptions() {
        if (this.assuredExceptionItem.icdList === undefined) {
            this.assuredExceptionItem.icdList = [];
        }
        if (this.assuredExceptionItem.medicamentGroupList === undefined) {
            this.assuredExceptionItem.medicamentGroupList = [];
        }
        if (this.assuredExceptionItem.medicamentList === undefined) {
            this.assuredExceptionItem.medicamentList = [];
        }
        if (this.assuredExceptionItem.procedureGroupList === undefined) {
            this.assuredExceptionItem.procedureGroupList = [];
        }
        if (this.assuredExceptionItem.procedureList === undefined) {
            this.assuredExceptionItem.procedureList = [];
        }
        this._usersservice.postUserExceptionsById(this.assuredExceptionItem, this.modalUser.id).subscribe(
            data => {
                this.assuredExceptionItem = data;
            },
            error => {
                console.log(error)
            }
        );
    }

    public getExceptions() {
        this._usersservice.getUserExceptionsById(this.modalUser.id).subscribe(
            data => {
                this.assuredExceptionItem = data;
            },
            error => {
                console.log(error)
            }
        );
    }

    public chooseCompany(option: any) {
        this.modalUser.insuredCompanyProductProgramId = null;
        this.showSpinerProduct = true;
        this.showSpinerProgram = true;
        this.filteredOptionsForProgram = null;
        this.controlForProduct.setValue({ name: '' });
        this.controlForProgram.setValue({ name: '' });
        this.getProductList(option.id);
    }

    public chooseProduct(option: any) {
        this.modalUser.insuredCompanyProductProgramId = null;
        this.showSpinerProgram = true;
        this.controlForProgram.setValue({ name: '' });
        this.getProgramList(option.id);
    }

    public chooseProgram(option: any) {
        this.modalUser.insuredCompanyProductProgramId = option.id;
    }
    public getProductList(id: number) {
        this._settingsService.getProductListByCompany(id).subscribe(
            data => {
                for (const value of data) {
                    this.optionsForProduct.push(value);
                }
                this.filteredOptionsForProduct = this._dateToSend.makeListForFilters(
                    this.controlForProduct, 'name', this.optionsForProduct, 0
                );
                this.showSpinerProduct = false;
                console.log(data);
            },
            error => {
                console.log(error);
            }
        );
    }
    public getProgramList(id: number) {
        this._settingsService.getProgramListByProduct(id).subscribe(
            data => {
                for (const value of data) {
                    this.optionsForProgram.push(value);
                }
                this.filteredOptionsForProgram = this._dateToSend.makeListForFilters(
                    this.controlForProgram, 'name', this.optionsForProgram, 0
                );
                this.showSpinerProgram = false;
                console.log(data);
            },
            error => {
                console.log(error);
            }
        );
    }
    ngOnInit() {
        if (this.idOfUser[0] !== 'n') {
            this.newUser = false;
            this._usersservice.getUserById(this.idOfUser).subscribe(
                data => {
                    this.modalUser = data;
                    this.controlForCompany.setValue({ companyName: this.modalUser.insuredCompanyName });
                    this.controlForProduct.setValue({ name: this.modalUser.insuredCompanyProductName });
                    this.controlForProgram.setValue({ name: this.modalUser.insuredCompanyProductProgramName });
                    this.getExceptions();
                },
                error => {
                    console.log(error);
                }
            );
        } else {
            this.readonly = false;
            this.newUser = true;
        }
        this._settingsService.getCompanyList([]).subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForCompany.push(value);
                }
                this.filteredOptionsForCompany = this._dateToSend.makeListForFilters(
                    this.controlForCompany, 'companyName', this.optionsForCompany, 0
                );
                this.showSpinerCompany = false;
            },
            error => {
                console.log(error);
            }
        );
    }

}
