import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Mkb10Service } from '../../service/mkb10.service';
import { SettingsService } from '../../service/settings.service';

@Component({
  selector: 'tmmed-coverage-list',
  templateUrl: './coverage-list.component.html',
  styleUrls: ['./coverage-list.component.scss']
})
export class CoverageListComponent implements OnInit{

    public idOfProgram: any = this.router.url.substring(
        this.router.url.lastIndexOf('programs/') + 9,
        this.router.url.lastIndexOf('/coverages')
    );

    public coverageList = [];
    public medicamentGroups: any[];
    public procedureGroups: any[];
    public showAddBtn = true;
    public nameOfProgram = '';
    
    constructor(
        private router: Router,
        private _mkb10Service: Mkb10Service,
        private _settingsservice: SettingsService
    ) { }

    public changeCoverage(coverage: any) {
        delete coverage.readonly;
        delete coverage.isAddingMed;
        delete coverage.isAddingService;
        this._settingsservice.postCoverageById(coverage).subscribe(
            data => {
                for (let cover of this.coverageList) {
                    if (cover.id === coverage.id) {
                        cover = coverage;
                        cover.readonly = true;
                        cover.isAddingMed = false;
                        cover.isAddingService = false;
                        console.log(coverage);
                    }
                }
                this.showAddBtn = true;
            },
            error => {
                coverage.readonly = true;
                console.log(error);
            }
        );
    }

    public updateCoverage(data: any, coverage: any) {
        for (let cover of this.coverageList) {
            if (cover === coverage) {
                cover = data;
            }
        }
    }

    public changeMedicament(idOfCoverage: number, medicamentGroup: any) {
        for (const coverage of this.coverageList) {
            if (idOfCoverage === coverage.id) {
                for (let medicament of coverage.medicamentGroups) {
                    if (medicamentGroup.id === medicament.id) {
                        medicament = medicamentGroup;
                        delete medicament.readonly;
                        this.changeCoverage(coverage);
                    }
                }
            }
        }
    }

    public addNewMedicament(coverage: any) {
        coverage.isAddingMed = true;
        this.showAddBtn = false;
    }
    public cancelAddingMed(coverage: any) {
        coverage.isAddingMed = false;
        this.showAddBtn = true;
    }

    public changeCoverageLocal(idOfCoverage: number, data: any) {
        for (let coverage of this.coverageList) {
            if (idOfCoverage === coverage.id) {
                // coverage = data;
                console.log(data);
            }
        }
    }

    public addNewProcedure(coverage: any) {
        coverage.isAddingService = true;
        this.showAddBtn = false;
    }
    public cancelAddingProcedure(coverage: any) {
        coverage.isAddingService = false;
        this.showAddBtn = true;
    }

    public updateList() {
        this._settingsservice.getCoveragesListByProgram(this.idOfProgram).subscribe(
            data => {
                this.coverageList = data;
                for (const coverage of this.coverageList) {
                    coverage.readonly = true;
                    coverage.isAddingMed = false;
                    coverage.isAddingService = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }
    ngOnInit() {
        this._mkb10Service.getListOfMedsGroup().subscribe(
            data => {
                this.medicamentGroups = data.content;
            },
            error => {
                console.log(error);
            }
        );
        this._mkb10Service.getListOfServiceGroup().subscribe(
            data => {
                this.procedureGroups = data.content;
            },
            error => {
                console.log(error);
            }
        );
        this.updateList();
        this._settingsservice.getProgramById(this.idOfProgram).subscribe(
            data => {
                this.nameOfProgram = data.name;
            },
            error => {
                console.log(error);
            }
        )
    }
}


