import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { DateToSendService } from 'src/app/service/date-to-send.service';
import { Mkb10Service } from 'src/app/service/mkb10.service';

@Component({
  selector: 'tmmed-user-exception-med-groups',
  templateUrl: './user-exception-med-groups.component.html',
  styleUrls: ['./user-exception-med-groups.component.scss']
})
export class UserExceptionMedGroupsComponent implements OnInit {

  @Input() exception: any;
  @Output() onUpdate = new EventEmitter<any>();

  public medGroups: any[] = [];
  public isAdding = false;
  public showSpinerMedGroup = true;

  public controlForMedGroup = new FormControl();
  public optionsForMedGroup: any[] = [];
  public filteredOptionsForMedGroup: Observable<any[]>;
  public tempOption = {
    id: null,
    medicamentGroupItem: {
      code: null,
      description: null,
      id: null,
      name: null
    }
  };

  constructor(
    public _dateToSend: DateToSendService,
    private _mkb10Service: Mkb10Service
  ) { }
  public clearInput() {
    this.controlForMedGroup.setValue({ name: '' });
    this.tempOption = {
    id: null,
    medicamentGroupItem: {
      code: null,
      description: null,
      id: null,
      name: null
    }
  };
  }
  public addGroup() {
    if (this.tempOption.medicamentGroupItem.id != null) {
      this.medGroups.unshift(this.tempOption);
      this.clearInput();
      this.isAdding = false;
      this.onUpdate.emit(this.medGroups);
    }
  }
  public deleteGroup(group: any) {
    this.medGroups.splice(this.medGroups.indexOf(group), 1);
      this.onUpdate.emit(this.medGroups);
  }
  public chooseOption(option: any) {
    this.tempOption.medicamentGroupItem = option;
  }

  ngOnInit() {
    this.medGroups = this.exception;
    this._mkb10Service.getListOfMedsGroup().subscribe(
      data => {
        for (const value of data.content) {
          this.optionsForMedGroup.push(value);
        }
        this.filteredOptionsForMedGroup = this._dateToSend.makeListForFilters(
          this.controlForMedGroup, 'name', this.optionsForMedGroup, 0
        );
        this.showSpinerMedGroup = false;
      },
      error => { console.log(error); }
    );
  }

}
