import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { DateToSendService } from 'src/app/service/date-to-send.service';
import { Mkb10Service } from 'src/app/service/mkb10.service';

@Component({
  selector: 'tmmed-setting-tree-med-add',
  templateUrl: './setting-tree-med-add.component.html',
  styleUrls: ['./setting-tree-med-add.component.scss']
})
export class SettingTreeMedAddComponent implements OnInit {

    @Output() onSave = new EventEmitter<boolean>();
    @Output() onCancel = new EventEmitter<boolean>();

    public showSpinerMed = true;
    public showSpinerMedGroup = true;

    public controlForMeds = new FormControl();
    public optionsForMeds: any[] = [];
    public filteredOptionsForMeds: Observable<any[]>;

    public controlForMedsCode = new FormControl();
    public optionsForMedsCode: any[] = [];
    public filteredOptionsForMedsCode: Observable<any[]>;

    public controlForMedGroup = new FormControl();
    public optionsForMedGroup: any[] = [];
    public filteredOptionsForMedGroup: Observable<any[]>;
    public med = {
        name: '',
        code: '',
        groupName: '',
        groupId: '',
        medicamentForm: '',
        id: '',
        isIncluded: false,
        isAgreementNeeded: false,
        onceFranchise: {
            id: '',
            maxSum: '',
            procent: ''
        },
        yearFranchise: {
            id: '',
            maxSum: '',
            procent: ''
        },
        onceLimit: {
            count: '',
            id: '',
            sum: ''
        },
        monthLimit: {
            count: '',
            id: '',
            sum: ''
        },
        yearLimit: {
            count: '',
            id: '',
            sum: ''
        }
    }

    public addNewMedicament() {
        this.onSave.emit();
    }
    public canceleAddNewMedicament() {
        this.onCancel.emit();
        this.controlForMeds.setValue({ name: '' });
        this.controlForMedGroup.setValue({ name: '' });
        this.med = {
            name: '',
            code: '',
            groupName: '',
            groupId: '',
            medicamentForm: '',
            id: '',
            isIncluded: false,
            isAgreementNeeded: false,
            onceFranchise: {
                id: '',
                maxSum: '',
                procent: ''
            },
            yearFranchise: {
                id: '',
                maxSum: '',
                procent: ''
            },
            onceLimit: {
                count: '',
                id: '',
                sum: ''
            },
            monthLimit: {
                count: '',
                id: '',
                sum: ''
            },
            yearLimit: {
                count: '',
                id: '',
                sum: ''
            }
        }
    }
    constructor(
        private _mkb10Service: Mkb10Service,
        public _dateToSend: DateToSendService
    ) {
        this._mkb10Service.getListOfMeds().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForMeds.push(value);
                    this.optionsForMedsCode.push(value);
                }
                this.filteredOptionsForMeds = this._dateToSend.makeListForFilters(this.controlForMeds, 'name', this.optionsForMeds, 3);
                this.filteredOptionsForMedsCode = this._dateToSend.makeListForFilters(
                    this.controlForMedsCode, 'code', this.optionsForMedsCode, 3
                );
                this.showSpinerMed = false;
            },
            error => { console.log(error); }
        );

        this._mkb10Service.getListOfMedsGroup().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForMedGroup.push(value);
                }
                this.filteredOptionsForMedGroup = this._dateToSend.makeListForFilters(
                    this.controlForMedGroup, 'name', this.optionsForMedGroup, 0
                );
                this.showSpinerMedGroup = false;
            },
            error => { console.log(error); }
        );
    }

    public chooseMedFromList(option: any) {
        this.controlForMedsCode.setValue({ code: option.code });
        this.controlForMedGroup.setValue({ name: option.groupName });
        this.med.name = option.name;
        this.med.code = option.code;
        this.med.groupName = option.groupName;
        this.med.groupId = option.groupId;
        this.med.medicamentForm = option.medicamentForm;
        this.med.id = option.id;
    }
    
    public chooseMedFromListCode(option: any) {
        this.controlForMeds.setValue({ name: option.name });
        this.controlForMedGroup.setValue({ name: option.groupName });
        this.med.name = option.name;
        this.med.code = option.code;
        this.med.groupName = option.groupName;
        this.med.groupId = option.groupId;
        this.med.medicamentForm = option.medicamentForm;
        this.med.id = option.id;
    }

    ngOnInit() {
    }

}
