import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'tmmed-setting-tree-med',
  templateUrl: './setting-tree-med.component.html',
  styleUrls: ['./setting-tree-med.component.scss']
})
export class SettingTreeMedComponent implements OnInit {
    @Input() medicament: any;
    @Output() onChanged = new EventEmitter<boolean>();

    public med: any;

    public makeDefault() {
        this.med.readonly = true;
        this.med.makeToDelete = false;
    }

    public makeToChange() {
        this.med.readonly = false;
        this.med.makeToDelete = false;
    }

    public makeToAskToDelete() {
        this.med.readonly = true;
        this.med.makeToDelete = true;
    }

    public console(event: any) {
        console.log(event);
    }

    constructor() { }

    ngOnInit() {
        this.med = this.medicament;
        this.med.readonly = true;
        this.med.makeToDelete = false;
    }
}
