import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';
import { DateToSendService } from '../../service/date-to-send.service';

@Component({
  selector: 'tmmed-company-list',
  templateUrl: './company-list.component.html',
  styleUrls: ['./company-list.component.scss']
})
export class CompanyListComponent implements OnInit {

    public companiesList = [];
    public amountOfCompanies: number;
    public typeOfLength: string
    public idOfDamage: string = this.router.url;
    public filtersToSend = [];

    constructor(
        private _settingsService: SettingsService,
        private router: Router,
        private _dateToSendService: DateToSendService
    ) { }

    public deleteCompany() {
        for (const company of this.companiesList) {
            if (company.selected === true) {
                this._settingsService.deleteCompanyById(company.id).subscribe(
                    data => {
                        this.updateList();
                    },
                    error => {
                        console.log(error);
                    }
                );
            }
        }
    }

    public changeSelection() {
        for (const company of this.companiesList) {
            company.selected = !company.selected;
        }
    }

    updateList() {
        this._settingsService.getCompanyList(this.filtersToSend).subscribe(
            data => {
                this.companiesList = data.content;
                this.amountOfCompanies = data.content.length;
                this.typeOfLength = this._dateToSendService.checkAmountToShow(this.amountOfCompanies);
                for (const company of this.companiesList) {
                    if (company.contactPerson.lastName) {
                        company.contactName = company.contactPerson.lastName + ' ' +
                                              company.contactPerson.firstName[0] + '. ' +
                                              company.contactPerson.middleName[0] + '.';
                    }
                    company.contactType = company.contactPerson.workType;
                    company.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }

    public setFilters(filters: any[]) {
        this.filtersToSend = filters;
        this.updateList();
    }

    ngOnInit() {
        this.updateList();
    }

}
