    public modalNameOne = '';
    public isAddingMed = false;
    public isAddingService = false;
    constructor(
        private _mkb10Service: Mkb10Service,
        private router: Router
    ) {}
    public showSpinerMkb = false;
    hasChild = (_: number, node: FlatNode) => node.expandable;
    private _transformer = (node: Node, level: number) => {
        return {
            expandable: !!node.nodes && node.nodes.length > 0,
            name: node.name,
            level: level,
            description: node.description,
            code: node.code,
            id: node.id
        };
    }

    public treeControl = new FlatTreeControl<FlatNode>(node => node.level, node => node.expandable);
    public treeFlattener = new MatTreeFlattener(this._transformer, node => node.level, node => node.expandable, node => node.nodes);
    public dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    // tree-end

    public modalForOne(modal: any) {
        this.modalNameOne = '( ' + modal.code + ' ) : ' + modal.name;
    }

    public updateList() {
        console.log('updated');
        this.isAddingMed = false;
        this.isAddingService = false;
    }

    public notUpdateList() {
        this.isAddingMed = false;
        this.isAddingService = false;
    }
    ngOnInit() {
        setTimeout(() => {
            this.showSpinerMkb = true;
          }, 100);
        this._mkb10Service.getMkb10().subscribe(
            data => {
                this.dataSource.data = data;
                this.showSpinerMkb = false;
            },
            error => {
                console.log(error);
            }
        );
    }
