import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';

@Component({
  selector: 'tmmed-company-id',
  templateUrl: './company-id.component.html',
  styleUrls: ['./company-id.component.scss']
})

export class CompanyIdComponent implements OnInit {
    public showAddContact: boolean;
    public showAddPayment: boolean;
    public readonly = true;
    public newCompany = false;
    public givenCompany: any =  {
        address: '',
        briefName: '',
        companyName: '',
        id: null,
        inn: '',
        maxOncePayment: null,
        mainContactPersons: null,
        contactPersons: [],
        paymentDetails: []
    };

    // company
    public currentCompany: any =  {};
    public idOfCompany: string = this.router.url.slice(11);

    public updateCurrentCompany(company: any) {
        this.currentCompany = company;
        if (this.currentCompany.contactPersons.length === 1 && this.currentCompany.mainContactPersonId === undefined) {
            this.currentCompany.contactPersons[0].main = true;
            this.currentCompany.mainContactPersonId = this.currentCompany.contactPersons[0].id;
            this.changeCompany();
        } else {
            for (const contact of this.currentCompany.contactPersons) {
                contact.readonly = true;
                contact.delete = false;
                if (contact.id === company.mainContactPersonId) {
                    contact.main = true;
                } else {
                    contact.main = false;
                }
            }
            for (const payment of this.currentCompany.paymentDetails) {
                payment.delete = false;
                payment.readonly = true;
            }
        }
    }

    updateCompany() {
        if (this.idOfCompany[0] === 'n') {
            this.currentCompany = this.givenCompany;
            this.newCompany = true;
            this.readonly = false;
            this.setShowButtons(false);
        } else {
            this.newCompany = false;
            this.readonly = true;
            this.setShowButtons(true);
            this._settingsService.getCompanyById(this.idOfCompany).subscribe(
                data => {
                    this.updateCurrentCompany(data);
                    this.sortContacts();
                },
                error => {
                    console.log(error);
                }
            );
        }
    }

    changeCompany() {
        const companyToSend: any = {
            address: this.currentCompany.address,
            briefName: this.currentCompany.briefName,
            companyName: this.currentCompany.companyName,
            id: this.currentCompany.id,
            inn: this.currentCompany.inn,
            maxOncePayment: this.currentCompany.maxOncePayment
        };
        if (this.currentCompany.mainContactPersonId !== undefined) {
            companyToSend.mainContactPersonId = this.currentCompany.mainContactPersonId;
        }
        this._settingsService.postCompanyById(companyToSend).subscribe(
            data => {
                if (this.newCompany === true) {
                    this.router.navigate(['../companies/', data], { replaceUrl: true });
                    this.setShowButtons(true);
                    this.currentCompany.id = data;
                }
            },
            error => {
                console.log(error);
            }
        );
    }

    // contact
    public sortContacts() {
        this.currentCompany.contactPersons.sort((a, b) => (a.main < b.main) ? 1 : ((b.main < a.main) ? -1 : 0));
    }

    public changeMainContact(choosenContact: any) {
        this.currentCompany.mainContactPersonId = choosenContact.id;
        for (const contact of this.currentCompany.contactPersons) {
            if (contact.id !== choosenContact.id) {
                contact.main = false;
            } else {
                contact.main = true;
            }
        }
    }

    public addNewContact() {
        const newContact: any = {
            firstName: '',
            lastName: '',
            middleName: '',
            workType: '',
            email: '',
            phone1: null,
            phone2: null,
            phone3: null,
            id: 0,
            delete: true,
            readonly: false
        }
        newContact.main = this.currentCompany.contactPersons.length === 0 ? true : false;

        this.currentCompany.contactPersons.unshift(newContact);
    }

    public sendContact(contact: any) {
        const contactToSend: any = {
            firstName: contact.firstName,
            lastName: contact.lastName,
            middleName: contact.middleName,
            workType: contact.workType,
            email: contact.email,
            phone1: contact.phone1,
            phone2: contact.phone2,
            phone3: contact.phone3,
            insuredCompanyId: this.currentCompany.id
        };
        if (contact.id !== 0) {
            contactToSend.id = contact.id;
        }
        this._settingsService.postContact(contactToSend).subscribe(
            data => {
                this.currentCompany.contactPersons[0].id = data;
                if (this.currentCompany.contactPersons.length === 1) {
                    this.currentCompany.mainContactPersonId = data;
                    this.changeCompany();
                }
                this.setShowButtons(true);
            },
            error => {
                console.log(error);
            }
        );
    }

    deleteContact(choosenContact: any) {
        const idTodelete = choosenContact.id;
        if (idTodelete === 0 ) {
            this.updateCompany();
            this.setShowButtons(true);
        }
        this._settingsService.deleteContact(idTodelete).subscribe(
            data => {
                this.updateCompany();
                this.setShowButtons(true);
            },
            error => {
                console.log(error);
            }
        );
    }
    // payment
    public addNewPayment() {
        const newPayment = {
            bankAccount: '',
            bankName: '',
            bik: '',
            address: '',
            id: 0,
            delete: true,
            readonly: false
        }
        this.currentCompany.paymentDetails.unshift(newPayment);
    }

    public sendPayment(payment: any) {
        const paymentToSend: any = {
            bankAccount: payment.bankAccount,
            bankName: payment.bankName,
            bik: payment.bik,
            address: payment.address,
            insuredCompanyId: this.currentCompany.id
        };
        if (payment.id !== 0) {
            paymentToSend.id = payment.id;
        }
        this._settingsService.postPayment(paymentToSend).subscribe(
            data => {
                this.currentCompany.paymentDetails[0].id = data;
                this.setShowButtons(true);
            },
            error => {
                console.log(error);
            }
        );
    }

    deletePayment(choosenPayment: any) {
        const idTodelete = choosenPayment.id;
        if ( idTodelete === 0 ) {
            for (const payment of this.currentCompany.paymentDetails) {
                if (payment.id === choosenPayment.id) {
                    this.currentCompany.paymentDetails = this.currentCompany.paymentDetails.filter((obj: any) => obj !== payment);
                }
            }
            this.setShowButtons(true);
        } else {
            this._settingsService.deletePayment(idTodelete).subscribe(
                data => {
                    for (const payment of this.currentCompany.paymentDetails) {
                        if (payment.id === choosenPayment.id) {
                            this.currentCompany.paymentDetails = this.currentCompany.paymentDetails.filter((obj: any) => obj !== payment);
                        }
                    }
                    this.setShowButtons(true);
                },
                error => {
                    console.log(error);
                }
            );
        }
    }
    setShowButtons(x: boolean) {
        this.showAddPayment = x;
        this.showAddContact = x;
    }

    constructor(
        private _settingsService: SettingsService,
        private router: Router
    ) {
        this.updateCompany();
        // this.updateCurrentCompany(this.givenCompany);
    }

    ngOnInit() {
    }

}
