/**
 * Node for to-do item
 */
export class TodoItemNode {
  nodes: TodoItemNode[];
  name: string;
  code: string;
  id: number;
}

/** Flat to-do item node with expandable and level information */
export class TodoItemFlatNode {
  name: string;
  code: string;
  id: number;
  level: number;
  expandable = false;
}

