 import { Component, OnInit } from '@angular/core';
 import { UsersService } from '../../service/users.service';
 import { DateToSendService } from '../../service/date-to-send.service';
 import { User } from '../../interface-and-class/filter.interface'
 import { Router } from '@angular/router';

 @Component({
  selector: 'tmmed-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})

export class UserListComponent implements OnInit {

    public User: User;
    public users: Array<any>;
    public amountOfUsers: any = null;
    public countOfUsers = 0;
    public nameOfDelete = '';
    public idOfDelete = 0;
    public typeOfAmountToShow = '';
    public filtersToSend = [];
    public typeOfFilters: string = null;
    public idForFilter: string = null;

    constructor(
        private _usersservice: UsersService,
        private _dateTosend: DateToSendService,
        private router: Router
    ) {}

    public getUserList() {
        this._usersservice.postUsers(this.filtersToSend).subscribe(
            data => {
                this.users = data.content;
                this.amountOfUsers = this.users.length;
                this.typeOfAmountToShow = this._dateTosend.checkAmountToShow(this.amountOfUsers);
                for (const  value of this.users) {
                    value.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }
    public getUserListByCompanyId() {
        this._usersservice.postUsersByCompanyId(this.filtersToSend, this.idForFilter).subscribe(
            data => {
                this.users = data.content;
                this.amountOfUsers = this.users.length;
                this.typeOfAmountToShow = this._dateTosend.checkAmountToShow(this.amountOfUsers);
                for (const  value of this.users) {
                    value.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }
    public getUserListByProductId() {
        this._usersservice.postUsersByProductId(this.filtersToSend, this.idForFilter).subscribe(
            data => {
                this.users = data.content;
                this.amountOfUsers = this.users.length;
                this.typeOfAmountToShow = this._dateTosend.checkAmountToShow(this.amountOfUsers);
                for (const  value of this.users) {
                    value.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }
    public getUserListByProgramId() {
        this._usersservice.postUsersByProgramId(this.filtersToSend, this.idForFilter).subscribe(
            data => {
                this.users = data.content;
                this.amountOfUsers = this.users.length;
                this.typeOfAmountToShow = this._dateTosend.checkAmountToShow(this.amountOfUsers);
                for (const  value of this.users) {
                    value.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        );
    }
    public setFilters(filters: any[]) {
        this.filtersToSend = filters;
        this.getUserList();
    }

    public checkAll() {
        for (const  value of this.users) {
            value.selected = !value.selected;
        }
    }

    public deleteUsers() {
        this.countOfUsers = 0;
        for (const  value of this.users) {
            if (value.selected) {
            this.countOfUsers++;
            }
        }
    }

    public deleteSelectedUsers() {
        for (const value of this.users) {
            if (value.selected) {
            this._usersservice.deleteUserById(value.id).subscribe(
                data => {
                    this.getUserList();
                },
                error => {
                    console.log(error);
                }
            );
            }
        }
    }

    public sortUsersUp() {
        this.users.sort((a, b) => (a.lastName > b.lastName) ? 1 : ((b.lastName > a.lastName) ? -1 : 0));
    }

    public sortUsersDown() {
        this.users.sort((a, b) => (a.lastName < b.lastName) ? 1 : ((b.lastName < a.lastName) ? -1 : 0));
    }

    ngOnInit() {
        this.typeOfFilters = this.router.url.substring(
            this.router.url.lastIndexOf('users/') + 6,
            this.router.url.lastIndexOf('/')
        );
        this.idForFilter = this.router.url.split('/').pop();
        if (this.typeOfFilters === 'company') {
            this.getUserListByCompanyId();
        } else if (this.typeOfFilters === 'product') {
            this.getUserListByProductId();
        } else if (this.typeOfFilters === 'program') {
            this.getUserListByProgramId();
        } else {
            this.getUserList();
        }
    }
}
