import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';




const LOSS: any = {
    assured: {},
    date: '',
    clinic: {},
    diagnosis: {
        code: '',
        description: '',
        id: null,
        name: ''
    },
    id: 0,
    medicaments: [],
    procedures: [],
    status: 'NEW'
}

@Injectable({
  providedIn: 'root'
})

export class LosesService {

  public serverUrl = environment.serverUrl;

  constructor(private httpClient: HttpClient) { }

  public getNew() {
    return LOSS;
  }

  public getLossesById( id: string): Observable<any>  {
    return this.httpClient.get(this.serverUrl + '/loss/' + id);
  }

  public getAllLossesForUser( id: string): Observable<any>  {
    return this.httpClient.get(this.serverUrl + '/assureds/' + id + '/losses');
  }

  public getLoses(array: any[]): Observable<any> {
    return this.httpClient.post(this.serverUrl + '/loss/list?paged=false', array);
  }

  public changeStatusMed( id: string, medId: string, status: any ): Observable<any> {
    return this.httpClient.post(this.serverUrl + '/loss/' + id + '/medicaments/' + medId + '/status', status);
  }

  public changeStatusService( id: string, medId: string, status: any ): Observable<any> {
    return this.httpClient.post(this.serverUrl + '/loss/' + id + '/procedures/' + medId + '/status', status);
  }

  public changeStatus(id: string, status: any): Observable<any> {
    return this.httpClient.post(this.serverUrl + '/loss/' + id + '/status', status, {
      observe: 'response'
    });
  }

  public addNewMed( id: string, newMed: any ): Observable<any>  {
    return this.httpClient.post(this.serverUrl + '/loss/' + id + '/medicaments', newMed);
  }

  public addNewService( id: string, newProcedure: any ): Observable<any>  {
    return this.httpClient.post(this.serverUrl + '/loss/' + id + '/procedures', newProcedure);
  }


  public postDamage( status: any ): Observable<any>  {
    return this.httpClient.post(this.serverUrl + '/loss', status);
  }

  public deleteMed( idOfLoss: string, idOfMed: string ): Observable<any>  {
    return this.httpClient.delete(this.serverUrl + '/loss/' + idOfLoss + '/medicaments/' + idOfMed);
  }

  public deleteService( idOfLoss: string, idOfServie: string ): Observable<any>  {
    return this.httpClient.delete(this.serverUrl + '/loss/' + idOfLoss + '/procedures/' + idOfServie);
  }

  public deleteLoss( id: string ): Observable<any>  {
    return this.httpClient.delete(this.serverUrl + '/loss/' + id);
  }
}
