import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { SettingsService } from 'src/app/service/settings.service';
import { Mkb10Service } from 'src/app/service/mkb10.service';

@Component({
  selector: 'tmmed-coverage-list-med',
  templateUrl: './coverage-list-med.component.html',
  styleUrls: ['./coverage-list-med.component.scss']
})
export class CoverageListMedComponent implements OnInit {

    @Input() medicament: any;
    @Input() coverageId: number;
    @Input() programId: number;
    @Output() onChanged = new EventEmitter<boolean>();

    public med: any = {
      id: null,
      medicamentGroupId: null,
      monthLimit: {
        count: null,
        id: null,
        sum: null
      },
      onceFranchise: {
        id: null,
        maxSum: null,
        procent: null
      },
      onceLimit: {
        count: null,
        id: null,
        sum: null
      },
      programCoverId: null,
      yearFranchise: {
        id: null,
        maxSum: null,
        procent: null
      },
      yearLimit: {
        count: null,
        id: null,
        sum: null
      }
    };
    public medicamentGroups: any[];
    public isReadonly = true;

    constructor(
        private _settingsservice: SettingsService,
        private _mkb10Service: Mkb10Service
    ) { }

    public deleteMed() {
      this._settingsservice.deleteMedicamentForCoverageById(this.coverageId, this.med.id).subscribe(
        data => {
          this.changeCoverage(data);
        },
        error => {
          console.log(error);
        }
      );
    }

    public changeMed() {
        const arrayToSend = [];
        arrayToSend.push(this.med);
        this._settingsservice.postMedicamentForCoverageById(arrayToSend, this.programId).subscribe(
            data => {
              this.changeCoverage(data);
              this.isReadonly = true;
            },
            error => {
                console.log(error);
            }
        );
    }

    public changeCoverage(data: any) {
      console.log(data);
      this.onChanged.emit(data);
    }

    ngOnInit() {
      this._mkb10Service.getListOfMedsGroup().subscribe(
        data => {
          this.medicamentGroups = data.content;
        },
        error => {
          console.log(error);
        }
      );
      for (const field in this.medicament) {
        if (this.medicament.hasOwnProperty(field)) {
          this.med[field] = this.medicament[field];
        }
      }
    }
}