import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsService } from '../../service/settings.service';
import { DateToSendService } from '../../service/date-to-send.service';

@Component({
  selector: 'tmmed-program-list',
  templateUrl: './program-list.component.html',
  styleUrls: ['./program-list.component.scss']
})
export class ProgramListComponent implements OnInit {

    public nameOfProduct = '';
    public programsList = [];
    public amountOfPrograms: number;
    public typeOfLength: string;
    public filtersToSend = [];
    public idOfProgram: any = this.router.url.substring(
        this.router.url.lastIndexOf('products/') + 9,
        this.router.url.lastIndexOf('/programs')
    );
    public showButton = false;
    public updateList() {
        this._settingsservice.getProgramListByProduct(this.idOfProgram).subscribe(
            data => {
                this.programsList = data;
                this.amountOfPrograms = data.length;
                this.typeOfLength = this._dateToSendService.checkAmountToShow(this.amountOfPrograms);
                if (this.programsList.length !== 0) {
                    this.showButton = false;
                } else {
                    this.showButton = true;
                }
                for (const program of this.programsList) {
                    program.selected = false;
                }
            },
            error => {
                console.log(error);
            }
        )
    }

    public setFilters(filters: any[]) {
        this.filtersToSend = filters;
        this.updateList();
    }

    public changeAll() {
        for (const program of this.programsList) {
            program.selected = !program.selected;
        }
    }
    public deleteSelected() {
        for (const program of this.programsList) {
            if (program.selected === true) {
                this._settingsservice.deleteProgramById(program.id).subscribe(
                    data => {
                        this.updateList();
                    },
                    error => {
                        console.log(error);
                    }
                );
            }
        }
    }
    constructor(
        private _settingsservice: SettingsService,
        private router: Router,
        private _dateToSendService: DateToSendService
    ) {
        this.updateList();
    }

    ngOnInit() {
        this._settingsservice.getProductById(this.idOfProgram).subscribe(
            data => {
                this.nameOfProduct = data.name;
            },
            error => {
                console.log(error);
            }
        )
    }

}
