import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { ModalService } from '../../interface-and-class/forloss.class';
import { Mkb10Service } from 'src/app/service/mkb10.service';
import { DateToSendService } from 'src/app/service/date-to-send.service';
import { LosesService } from 'src/app/service/loses.service';
import { Router } from '@angular/router';

@Component({
  selector: 'tmmed-loss-id-procedure-add',
  templateUrl: './loss-id-procedure-add.component.html',
  styleUrls: ['./loss-id-procedure-add.component.scss']
})
export class LossIdProcedureAddComponent implements OnInit {

    @Output() onChanged = new EventEmitter<boolean>();

    public modalService = new ModalService();
    public showModalSaveService = false;
    public idOfLoss: string = this.router.url.slice(8);

    public controlForService = new FormControl();
    public optionsForService: any[] = [];
    public filteredOptionsForService: Observable<any[]>;

    public controlForServiceCode = new FormControl();
    public optionsForServiceCode: any[] = [];
    public filteredOptionsForServiceCode: Observable<any[]>;

    public controlForServiceGroup = new FormControl();
    public optionsForServiceGroup: any[] = [];
    public filteredOptionsForServiceGroup: Observable<any[]>;

    public showSpinerService = true;
    public showSpinerServiceGroup = true;

    public isDisabledServiceInfo = true;

    constructor(
        private _mkb10Service: Mkb10Service,
        public _dateToSend: DateToSendService,
        private _losesservice: LosesService,
        private router: Router
    ) {
        this._mkb10Service.getListOfService().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForService.push(value);
                    this.optionsForServiceCode.push(value);
                }
                this.filteredOptionsForService = this._dateToSend.makeListForFilters(
                    this.controlForService, 'name', this.optionsForService, 3
                );
                this.filteredOptionsForServiceCode = this._dateToSend.makeListForFilters(
                    this.controlForServiceCode, 'code', this.optionsForServiceCode, 3
                );
                this.showSpinerService = false;
            },
            error => { console.log(error); }
        );

        this._mkb10Service.getListOfServiceGroup().subscribe(
            data => {
                for (const value of data.content) {
                    this.optionsForServiceGroup.push(value);
                }
                this.filteredOptionsForServiceGroup = this._dateToSend.makeListForFilters(
                    this.controlForServiceGroup, 'name', this.optionsForServiceGroup, 0
                );
                this.showSpinerServiceGroup = false;
            },
            error => { console.log(error); }
        );
    }

    public checkModalServiceSave() {
        if (this.modalService.procedureSaveItem.name === '' ||
            this.modalService.stateId === 0 ||
            this.modalService.procedureSaveItem.groupName === '' ||
            this.modalService.date === '' ||
            this.modalService.procedureSaveItem.name === null ||
            this.modalService.procedureSaveItem.groupName === null) {
            this.showModalSaveService = false;
        } else {
            this.showModalSaveService = true;
        }
    }

    public chooseServiceFromList(option: any) {
        this.controlForServiceCode.setValue({ code: option.code });
        this.controlForServiceGroup.setValue({ name: option.groupName });
        this.modalService.procedureSaveItem.name = option.name;
        this.modalService.procedureSaveItem.code = option.code;
        this.modalService.procedureSaveItem.groupName = option.groupName;
        this.modalService.procedureSaveItem.groupId = option.groupId;
        this.modalService.procedureSaveItem.id = option.id;
    }

    public chooseServiceFromListCode(option: any) {
        this.controlForService.setValue({ name: option.name });
        this.controlForServiceGroup.setValue({ name: option.groupName });
        this.modalService.procedureSaveItem.name = option.name;
        this.modalService.procedureSaveItem.code = option.code;
        this.modalService.procedureSaveItem.groupName = option.groupName;
        this.modalService.procedureSaveItem.groupId = option.groupId;
        this.modalService.procedureSaveItem.id = option.id;
    }

    public addNewService() {
        this.modalService.amount = this.modalService.costPerUnit * this.modalService.quantityAmount;
        this.modalService.date = this._dateToSend.makeDateToSend(this.modalService.date);
        const arrayToSend = [];
        arrayToSend.push(this.modalService);
        this._losesservice.addNewService(this.idOfLoss, arrayToSend).subscribe(
            data => {
                this.onChanged.emit(data);
                this.modalService = new ModalService();
                this.clearModal();
            },
            error => { console.log(error); }
        );
    }
    public clearModal() {
        this.controlForService.setValue({ name: '' });
        this.controlForServiceCode.setValue({ code: '' });
        this.controlForServiceGroup.setValue({ name: '' });
    }

    ngOnInit() {
    }

}
