import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, Event, NavigationStart, NavigationEnd, NavigationError, ActivatedRoute } from '@angular/router';
import { ConnectedPositionStrategy } from '@angular/cdk/overlay';

@Component({
  selector: 'tmmed-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

    showSearch = false;
    activeCompany = false;
    activeProduct = false;
    activeProgram = false;
    activeCoverage = false;
    activeSettings = false;
    urlForCompany = '';
    urlForProduct = '';
    urlForProgram = '';
    urlForCoverage = '';

    showExtraMenu = false;
    route: ActivatedRoute;
    constructor(private _location: Location, private router: Router)  {

        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationStart) {
                const url = event.url;
                if (url.includes('companies')) {
                    this.showExtraMenu = true;
                    if (url.includes('companies')) {
                        this.activeCompany = true;
                        this.urlForCompany = '/companies'
                    } else {
                        this.activeCompany = false;
                    }
                    if (url.includes('products')) {
                        this.activeProduct = true;
                        this.urlForProduct = url.substring(0, url.indexOf('products') + 8);
                    } else {
                        this.activeProduct = false;
                    }
                    if (url.includes('programs')) {
                        this.activeProgram = true;
                        this.urlForProgram = url.substring(0, url.indexOf('programs') + 8);
                    } else {
                        this.activeProgram = false;
                    }
                    if (url.includes('coverages')) {
                        this.activeCoverage = true;
                        this.urlForCoverage = url.substring(0, url.indexOf('coverages') + 9);
                    } else {
                        this.activeCoverage = false;
                    }
                    if (url.includes('settings')) {
                        this.activeSettings = true;
                    } else {
                        this.activeSettings = false;
                    }
                } else {
                    this.showExtraMenu = false;
                }
            }
        });

    }
    backClicked() {
        this._location.back();
    }

    ngOnInit() {
    }
}
