import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { LosesService } from '../../service/loses.service';

@Component({
  selector: 'tmmed-user-id-loss',
  templateUrl: './user-id-loss.component.html',
  styleUrls: ['./user-id-loss.component.scss']
})
export class UserIdLossComponent implements OnInit {

    @Input() idOfUser: string;

    public listOfDamages = [];
    public showListOfDamages = false;

    constructor(
        private _losesService: LosesService,
        private router: Router
    ) { }

    ngOnInit() {
        if (this.idOfUser[0] !== 'n') {
            this._losesService.getAllLossesForUser(this.idOfUser).subscribe(
                data => {
                    if (data.length !== 0 ) {
                        this.listOfDamages = data;
                        this.showListOfDamages = true;
                    }
                },
                error => {
                    console.log(error);
                }
            );
        }
    }

}
